<template>
  <div style="width:100%;height:100%">
    <div class="menu-top">团队管理</div>
    <div class="table-top radius_8">
      <div class="table-top-left">
        <div class="table-top-left-top">
          <div class="div_p">
            <span class="font_size_20">团队</span>
            <el-select class="inp" v-model="sel.team_name" placeholder="请选择">
              <el-option v-for="(item,index) in options" :key="index" :label="item==null?'全部':item" :value="item"></el-option>
            </el-select>
          </div>
          <div class="div_p">
            <span class="font_size_20">销售经理</span>
            <el-input class="inp" v-model="sel.leader" placeholder="请输入内容"></el-input>
          </div>
           <div class="div_p">
            <span class="font_size_20"></span>
            <div class="inp"></div>
          </div>
        </div>
      
      </div>
      <div class="table-top-rig">
        <el-button @click="reset()" class="but">重置</el-button>
        <el-button @click="changeDate()" class="but" type="primary">查询</el-button>
      </div>
    </div>

    <div class="menu-small-top">
      <span class="font_size_weight_600">团队列表</span>
      <el-button v-if="user['auth']!=300"  class="but" @click="add" style="float:right" type="primary">添加</el-button>
    </div>
    <div class="table-btn">
      <template>
        <el-table :data="tableData" style="width: 100%; " class="font_size_16">
          <!-- type="index" :index="indexMethod" -->
          <el-table-column label="序号" width="100" align="center" prop="NO"></el-table-column>
          <!-- <el-table-column  width="80" align="center" prop="NO"></el-table-column> -->
          <el-table-column prop="team_name" label="团队名" width="220"></el-table-column>
          <el-table-column prop="create_time" label="创建时间" width="220"></el-table-column>
          <el-table-column prop="leader" width="220" label="销售经理"></el-table-column>
          <el-table-column prop="saller_tel" width="220" label="销售经理联系方式"></el-table-column>
          <el-table-column prop="nums" width="220" label="团队人数"></el-table-column>
          <el-table-column prop="build_name" width="220" label="项目"></el-table-column>
          <!-- <el-table-column
                prop="address"
                 width="200"
                label="项目">
          </el-table-column>-->

          <el-table-column label="操作">
            <template slot-scope="scope" >
              <el-button  class="font_size_14"   @click="showTab(scope.row)" type="text" size="small" >查看详情</el-button>
               <el-button  class="font_size_14" v-if="user['auth']!=300"  @click="set(scope.row)" type="text" size="small">编辑</el-button>
              <el-button  class="font_size_14" v-if="user['auth']!=300"  @click="del(scope.row)" type="text" size="small">删除</el-button>

            </template>
          </el-table-column>
        </el-table>
      </template>
      <div  class="page">
        <el-pagination
      
        background
        layout="total,  prev, pager, next, jumper"
        @current-change="handleCurrentChange"
        :page-size="sel.pre_page"
        :current-page="sel.page_num"
        :total="total"
      ></el-pagination>
      </div>
    
      <!--       :page-count="count" -->
    </div>
    <el-dialog 
     :modal-append-to-body='false'
    :title="tabName" :visible.sync="isDia" width="420px" :before-close="closeSet">
     <el-form  :model="data" :rules="rules" ref="ruleForm" label-width="130px" class="demo-ruleForm">
          <el-form-item label="团队名称" prop="team_name">
             <el-input  class="inp" v-model="data.team_name" placeholder="请输入"></el-input>
          </el-form-item>
           <el-form-item label="销售经理">
              <el-select class="inp" v-model="data.leader" placeholder="请选择">
                <el-option v-for="(item,index) in leaders" :key="index" :label="item==null?'请选择':item" :value="item"></el-option>
              </el-select>
           </el-form-item>
     </el-form>
      <!-- <div class="upPaw">
          <div class="upPaw_pa">
          <span>团队名称</span>
          <el-input  class="inp" v-model="data.team_name" placeholder="请输入"></el-input>
        </div>
       
          <div class="upPaw_pa">
          <span>销售经理</span>
          <el-select class="inp" v-model="data.leader" placeholder="请选择">
            <el-option v-for="(item,index) in leaders" :key="index" :label="item==null?'请选择':item" :value="item"></el-option>
          </el-select>
        </div>
         
      
      
      </div> -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeSet()">取 消</el-button>
        <el-button type="primary" @click="upOk('ruleForm')" :loading="isLoad">确 定</el-button>
      </span>
    </el-dialog>
     
  </div>
</template>

<script>
import {
  getTeams,
  selectGroups,
  addTeam,
  delTeam,
  updateTeam,
  getTeam,
  getLeader,
  
} from "@/api/getData";
import { setStore, getStore } from "@/config/mUtils";
export default {
  name: "team",

  data() {
    return {
      // 可修改不可修改
      dis: false,
      isLoad: false,
      // count: 10, //总页数
      total: 100, //总条数
      value: "",
      radio: "",
      time: "",
      isDia: false,
      // 用户
      user:null,
      sel: {
        team_name: null,
        leader: null,
        pre_page: 10, //每页条数
        page_num: 1 //第几页
      },
      tabName: "创建团队",
      data: {
        team_id: null,
        team_name : null,
        leader: null
      },
      rules:{
           team_name: [
            { required: true, message: '团队名称不可为空', trigger: 'blur' },
          ],
         
      },
      options: [],
      leaders: [],
      tableData: []
    };
  },
  components: {},
  computed: {},
  async created() {
      this.getUser();
      // this.handleListener();
  },
  async mounted() {
    // 获取数据
    if (this.$route.query.sel) {
      this.sel=this.$route.query.sel;
    } 
    await this.changeDate();
    await this.chanTeam();
  },

  activated() {
    // this.handleListener();
  },
  deactivated() {
    // window.removeEventListener('resize', this.renderChart);
    // bus.$off('collapse', this.handleBus);
  },
  methods: {
    // 初始化
    init() {
      this.isLoad = false;
      this.data = {
        team_id: null,
        team_name : null,
        leader: null
      };
    },
    async changeDate() {
       // 查询数据
       const res = await getTeams(this.sel);
      //  console.log(res);
        if (res.code != 0) {
          this.$message({
            type: "error",
            message: res.msg
          });
          return;
        }
        this.tableData = res.data.list;
        this.total = res.data.total;
    },
    indexMethod(index) {
      return index + 1 * 1;
    },
    handleCurrentChange(val) {
      //页数发生变化
      // alert(`当前页: ${val}`);
      this.sel.page_num = val;
      this.changeDate();
    },
    async set(ent) {
      this.tabName = "修改";
      await this.chanLeader();
      this.dis = true;
      const res = await getTeam({ team_id: ent.team_id });
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
      this.data = res.data;
      // console.log(this.data);
      this.isDia = true;
      this.isLoad = false;
    },
    async del(ent) {
      this.$confirm("您确定要删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(async () => {
        const res = await delTeam({ team_id: ent.team_id });
        // console.log(jsondata['build_id'] )
        if (res.code != 0) {
          this.$message({
            type: "error",
            message: res.msg
          });
          return;
        }
        this.$message({
          type: "success",
          message: "删除成功!"
        });
        this.changeDate();
      });
    },
    async showTab(ent){
      // 查看详情
       this.$router.push({name:'teamDetails', query: { team_id: ent.team_id },params: { sel: this.sel }});

      //  this.$router.push({path:'/teamDetails', query: { team_id: ent.team_id }});
      // this.$root('teamDetails')
    },
   async add() {
      this.isDia = true;
      this.tabName = "添加";
       await this.chanLeader();
      this.init();
      this.dis = false;
    },
    closeSet() {
      this.isDia = false;
       this.$refs['ruleForm']?this.$refs['ruleForm'].resetFields():'';
    },
    async upOk(formName) {
        this.$refs[formName].validate(async (valid) => {
        if(valid){
          if (!this.data.team_name) {
              this.$message({
                type: "error",
                message: "团队名称不可为空"
              });
              return;
            }
          this.isLoad = true;
          let res;
          if (this.tabName == "添加") {
            res = await addTeam(this.data);
          } else {
            res = await updateTeam(this.data);
          }

          this.isLoad = false;
          // console.log(jsondata['build_id'] )
          if (res.code != 0) {
            this.$message({
              type: "error",
              message: res.msg
            });

            return;
          }
          this.$message({
            type: "success",
            message: "设置成功"
          });
          this.init();
          this.changeDate();
          this.isDia = false;
         }
       });
    },
    reset() {
      var te = this.sel;
      // console.log(te)
      te.leader = null;
      te.team_name = null;
      this.sel = te;
    },
    getUser(){
       var data = getStore("user");
      var jsondata = data ? JSON.parse(data) : {};
      this.user=jsondata;

    },
    // 获取团队
    async chanTeam() {
     
      const res = await selectGroups({ build_id: this.user["build_id"] });
      // console.log(jsondata['build_id'] )
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
       if (Array.isArray(res.data)){
      //    console.log(res.data);
           this.options = res.data;
       }
       else{
         this.options=[];
      }
      this.options.unshift(null);
      // console.log(res);
    },
    // 获取领导
    async chanLeader(){
         const res = await getLeader({});
      // console.log(jsondata['build_id'] )
      if (res.code != 0) {
        this.$message({
          type: "error",
          message: res.msg
        });
        return;
      }
        if (Array.isArray(res.data)){
    
         this.leaders = res.data;
      }
      else{
         this.leaders=[];
      }
     
      // console.log( this.leaders);
      this.leaders.unshift(null);
      
    }
  }
};
</script>


<style scoped>
.grid-content {
  background-color: black;
}
.table-top {
  display: flex;
  justify-content: space-between;
}
.table-top-left {
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.table-top-rig {
  width: 17%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.table-top-left-top,
.table-top-left-btn {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.div_p {
  width: 26%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.inp {
  width: 200px;
}

.page {
  display: flex;
  height: 38px;
  margin-right: 10px;
  align-items: center;
  flex-direction: row-reverse;

  /* float: right; */
}
/* 修改 */
.upPaw {
  width: 90%;
  margin-left: 4%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.upPaw div span {
  font-size: 20px;
  /* font-weight: 600; */
  width: 35%;
}
.upPaw_pa {
  width: 100%;
  height: 30%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}
.set-role {
  margin-top: 20px;
  width: 100%;
  height: 30%;
  /* background: rosybrown; */
  display: flex;
}
.set-role span {
}
.set-role-right {
  vertical-align: middle;
  width: 200px;
  /* background: royalblue; */
}
</style>
<style >
.el-table__header {
  padding: 5px 0;
}
.el-table__body-wrapper .el-table__row td {
  padding: 5px 0;
}
.el-input__inner {
  height: 37px;
}
.el-input__icon {
  line-height: 37px;
}
.el-radio {
  margin-top: 5px;
}
.el-radio {
  margin-right: 20px;
}
</style>



